<template>
	<div class="error_banner">
		<h1 class="error_banner__title">
			{{ error[code].title }}
		</h1>
		<p class="error_banner__text">
			{{ error[code].text }}
		</p>
		<BaseButton
			v-if="error[code].goBack"
			:color="'white'"
			@click="goBack"
		>
			Вернутся на главную
		</BaseButton>
	</div>
</template>

<script>
import BaseButton from '@/components/BaseButton.vue';
export default {
	components: {
		BaseButton
	},
	data() {
		return {
			error: {
				404: {
					title: '404',
					text: 'Страница устарела, была удалена или не существовала вовсе',
					goBack: true
				},
				500: {
					title: '500',
					text: 'Что-то пошло не так',
					goBack: false
				},
				502: {
					title: '502',
					text: 'Сервер в данный момент недоступен',
					goBack: false
				},
				503: {
					title: '503',
					text: 'Сервер в данный момент недоступен',
					goBack: false
				},
				
			}
		};
	},
	computed: {
		code () {
			return this.$route.params.code ? this.$route.params.code : 404;
		}
	},
	methods: {
		goBack() {
			this.$router.push({
				name: 'Home',
			});	
		}
	}
};
</script>


<style lang="scss" scoped>
	.error_banner {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		background-image: url('@/assets/404/bg.png');
		background-repeat: no-repeat;
		color: white;
		&__title {
			font-size: 3em;
			margin-bottom: 2rem;
		}
		&__text {
			margin-bottom: 2.5rem;
		}
	}
</style>